html {
    height: 100%;
}

body{
  font-family: 'Noto Sans Display', sans-serif;
  font-family: 'Open Sans', sans-serif;
  /* background-color: rgb(253, 248, 254); */
  /* background-image: linear-gradient(red, yellow) */
  height: 100%;
  margin: 0;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background: linear-gradient(rgb(254, 252, 255), rgb(248, 215, 255))
}

section{
  display: flex;
  flex-direction: column;
}

header{
  display: flex;
  align-items: center;
  justify-content: center;
}

#picture img{
  /* width: 480px;
  height: 480x; */
  border-radius: 5%;
}

#checkbox-render{
  display: flex;
  margin-right: auto;
  margin-left: auto;
  justify-content: center;
  width: 1000px;
  height: 40px;
  /* // border: 1px rgb(249, 145, 145) solid;
  // border-radius: 20px; */
}

#select-box-render{
  display: flex;
  margin-right: auto;
  margin-left: auto;
  justify-content: center;
  width: 100px;
  /* // border: 1px rgb(249, 145, 145) solid;
  // border-radius: 20px; */
}

#filter-box-render{
  display: flex;
  margin-right: auto;
  margin-left: auto;
  justify-content: center;
  width: 50%;
  /* // border: 1px rgb(249, 145, 145) solid;
  // border-radius: 20px; */
}

#filter-form{
  display:flex;
  flex-direction: column;
}

.choices{
  width: 500px;
}

#title-render{
  display: flex;
  justify-content: center;
  width: 100%;
  font-size: 70px;
  font-weight: bold;
}

#region-render{
  display: flex;
  margin-right: auto;
  margin-left: auto;
  justify-content: center;
  width: 1000px;
  height: 50px;
  /* // border: 1px rgb(249, 145, 145) solid;
  // border-radius: 20px; */
  font-size: 30px;
}

#pic-ingred-render{
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  height: 500px;
  /* // border: 1px rgb(249, 145, 145) solid;
  // border-radius: 20px; */
}

#info-render{
  display: flex;
  margin-right: auto;
  margin-left: auto;
  justify-content: space-evenly;
  width: 100%;
  /* // border: 1px rgb(249, 145, 145) solid;
  // border-radius: 20px; */
}

#ingredient-list{
  padding-right: 28px;
}

#ingredients{
  display: block;
  width: 50%;
}

#directions{
  display: flex;
  flex-direction: column;
  width: 100%;
}

#directions-list{
  padding-right: 20px;
}

.pic-ingred1{
  display: inline-flex;
  justify-self: center;
  align-self: center;
}
.pic-ingred2{
  display: flex;
  width: 100%;
  justify-self: center;
  align-self: center;
  flex-direction: column;
}

#search-submit{
  justify-self: center;
}

input[type=submit], select{
  font-family: 'Noto Sans Display', sans-serif;
  font-family: 'Open Sans', sans-serif;
}

.hide{
  display: none !important;
}

.button{
  display:inline-block;
  padding:0.35em 1.2em;
  border:0.1em solid #f581a5;
  margin:0 0.3em 0.3em 0;
  border-radius:0.12em;
  box-sizing: border-box;
  text-decoration:none;
  font-family:'Roboto',sans-serif;
  font-weight:300;
  color:#f581a5;
  text-align:center;
  transition: all 0.2s;
  background-color: white;
}

.button:hover{
  color:#ffffff;
  background-color:#f581a5;
}

@media all and (max-width:30em){
  .button{
  display:block;
  margin:0.4em auto;
  }
}

.button-box{
  display: flex;
  justify-content: center;
}

#prep-info{
  display: flex;
  justify-content: space-evenly;
}

#prep-ready{
  /* border-left: 1px solid;
  border-right: 1px solid; */
  /* padding-left: 10%;
  padding-right: 10%; */
}

.prep-info{
  display:flex;
  flex-direction: column;
  width: 33%;
  align-items: center;
}

.line-div{
  display: flex;
  width:0.5px;
  border: 1px solid black;
}

.number-info{
  font-size: 50px;
}

.prep-text{
  font-size: small;
  color: grey;
}

#source-url{
  display: flex;
  align-self: flex-end;
  align-items: flex-end;
  height:100px;
}

.title{
  font-family: 'Licorice', cursive;
}

.source-url{
  padding-right: 50px;
}